@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app {
  min-width: 320px;
  max-width: 1440px;
  background-image: url(/weather-app/static/media/app.1aaf364f.jpg);
  background-size: cover;
  background-position: center;
  transition: 0.2s ease;
}

.app_warm {
  background-image: url(/weather-app/static/media/app-warm.8f1a86f8.jpg);
}

.app_cold {
  background-image: url(/weather-app/static/media/app-cold.8013a239.jpg);
}

.root {
  font-family: 'Montserrat', sans-serif;
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.75));
  padding: 50px;
}

.search {
  max-width: 600px;
  margin: 0 auto 75px;
  position: relative;
}

.search__input {
  color: black;
  font-size: 15px;
  display: block;
  width: 100%;
  padding: 15px;
  border: none;
  outline: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border-radius: 50px;
  background: rgba(255, 255, 255, 0.52);
  transition: 0.4s ease;
}

.search__input:focus {
  background-color: white;
}

.search__button {
  font-weight: 500;
  font-size: 18px;
  position: absolute;
  right: 0;
  top: 0;
  background-color: #e5d53f;
  box-shadow: 0 3px 10px rgb(103, 103, 103);
  color: #2b2b2b;
  padding: 13px 54px;
  border-style: none;
}

.search__button:hover {
  background-color: #ffed47;
  transition: .1s linear;
}

.search__button:active {
  background-color: #d6c73b;
  transition: .1s linear;
}

.location {
  color: white;
  font-size: 36px;
  text-align: center;
}

.location__date {
  color: white;
  font-size: 20px;
  text-align: center;
}

.weather {
  text-align: center;
}

.weather__temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  padding: 15px 25px;
  color: #FFF;
  font-size: 102px;
  font-weight: 900;
  text-align: center;
}

.weather__description {
  color: #FFF;
  font-size: 48px;
  font-weight: 700;
}

.text-title {
  text-align: center;
  color: #FFF;
  font-size: 48px;
  font-weight: 700;
}

.button {
  cursor: pointer;
  box-sizing: border-box;
  border-radius: 100px;
  outline: none;
}

@media screen and (min-width: 397px) and (max-width: 490px) {
  .weather__description {
    font-size: 36px;
  }
}

@media screen and (max-width: 396px) {
  .weather__description {
    font-size: 26px;
  }
}

@media screen and (max-width: 475px) {
  .search__button {
    margin-top: 15px;
    position: static;
    width: 100%;
  }
}

